.footer{
    display:flex;
    justify-content: center;
    align-items:center;
    background-color: aquamarine;
    max-width: 90%;
    margin:auto;
    height:40px;
    border-top-left-radius:24px ;
    border-top-right-radius:24px ;
    padding:12px;
    color:#000;
}

@media only screen and (max-width:720px){
    .footer{
        font-size:12px;
    }
}